<template>
  <div class="body">
    <img :src="url" alt="" class="gif one">
    <img :src="url1" alt="" class="gif two">
    <img :src="url2" alt="" class="gif three">
    <img :src="url3" alt="" class="gif four">
    <img :src="url4" alt="" class="gif five">
    <img :src="url5" alt="" class="gif six">
    <img :src="url6" alt="" class="gif seven">
    <img :src="url7" alt="" class="gif eight">
  </div>
</template>

<script>
export default {
  name: 'tag_1',
  data () {
    return {
      url: require('../assets/img/GIF/1.1.gif'),
      url1: require('../assets/img/GIF/1.2.gif'),
      url2: require('../assets/img/GIF/1.3.gif'),
      url3: require('../assets/img/GIF/1.4.gif'),
      url4: require('../assets/img/GIF/1.5.gif'),
      url5: require('../assets/img/GIF/1.6.gif'),
      url6: require('../assets/img/GIF/1.7.gif'),
      url7: require('../assets/img/GIF/1.8.gif')
    }
  },
  components: {

  },
  mounted () {
    $('.weibo').hover(function () {
      $('.one').css('display', 'block')
    }, function () {
      $('.one').css('display', 'none')
    })
    $('.baidu').hover(function () {
      $('.two').css('display', 'block')
    }, function () {
      $('.two').css('display', 'none')
    })
    $('.alipay').hover(function () {
      $('.three').css('display', 'block')
    }, function () {
      $('.three').css('display', 'none')
    })
  }
}
</script>

<style scoped>
.body{
  margin-left: 20px;
  margin-top: 17px;
  padding: 12px 30px 12px 30px;
  border: solid 3px black;
  width: 75vw;
  border-radius: 10px;
  display: flex;
  position: relative;
}
.gif{
  position:absolute;
  width: 20vw;
  display: none;
  border-radius: 10px;
}
.one{top:-10vh;left:10vw;}
.two{top:-10vh;left:30vw;}
.three{top:-10vh;right:10vw;}
</style>



